import React,{useState,useEffect} from 'react'

export default function Select() {

    const [dogs,setDogs]=useState([]);
    const [breed,setBreed]=useState([]);
    const[dogurl,setDogurl]=useState()
    const[dogimg,setDogimg]=useState([])
    const [dogbreed,setDogbreed]=useState()

    useEffect(() => {
        fetch('https://dog.ceo/api/breeds/list/all')
        .then(result=>result.json())
        .then(data=>setDogs(data.message));
        fetch(dogurl).then(res=>res.json()).then((data)=>{setDogimg(data.message)});
    }, [dogurl])

    function changed(event){
        setBreed(dogs[event.target.value]);
        setDogbreed('https://dog.ceo/api/breed/'+event.target.value+'/')
        setDogurl('https://dog.ceo/api/breed/'+event.target.value+'/images')
    }

    function breedselected(event){
        const breedurl=dogbreed+event.target.value+'/images';
        setDogurl(breedurl);
    }

    return (
        <div>
            <div className="select container my-2">
                <select  onChange={changed} name="dog">
                <option selected disabled hidden>
                        select the breed
                </option>
                    {Object.keys(dogs).map((element,i)=>{
                     return <option key={i} value={element}>{element}</option>
                    })}
                </select>
                {breed.length>=1?
                <select onChange={breedselected} className="mx-5">
                    {breed.map((dogbreed)=>{
                    return <option  key={dogbreed} value={dogbreed}>{dogbreed}</option>
                    })}
                </select>:null}
                <div className="row">
                    
                {dogimg.length>=1?dogimg.map((url)=>{
                    return <div className="col-sm-3 col-12"> <img src={url} style={{marginRight:'2px'}} className="my-1 m-auto" width="250px" height="250px" key={url} alt="not found" />
                    </div>
                }):<h5>No Images To Show</h5>}
                </div>
            </div>
        </div>
    )
}
